 * @Description: In User Settings Edit
 * @Author: your name
 * @Date: 2019-08-29 10:32:48
 * @LastEditTime: 2019-09-06 15:01:22
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="menAddress_wrapper">
    <Header></Header>
    <div class="menaddress_content">
      <div class="cru_content">
        <bread-crumb :crumbs="crumbs"></bread-crumb>
      </div>
      <el-row class="menaddress_container">
        <el-col class="add_address_wrapper">
          <div class="add_address_content">
            <div class="coin_kinds">
              <p class="com_title">币种</p>
              <el-select v-model="coinVal" placeholder="请选择" @change="sel_lcos('coin')">
                <el-option v-for="(item,index) in coins" :key="index" :label="item.symbol" :value="item.symbol"></el-option>
              </el-select>
            </div>
            <div class="link_kinds">
              <p class="com_title">链名称</p>
              <el-select v-model="linkVal" placeholder="请选择" @change="sel_lcos('links')">
                <el-option v-for="(item,index) in links" :key="index" :label="item.chain" :value="item.chain"></el-option>
              </el-select>
            </div>
            <div class="mention_address">
              <p class="com_title">提币地址</p>
              <el-input type="text" v-model="addressVal"></el-input>
            </div>
            <div class="remark">
              <p class="com_title">备注</p>
              <el-input type="text" v-model="remark"></el-input>
            </div>
          </div>
          <div class="add_btn_content">
            <el-button @click.native="addAddress" v-loading="addAddressLoad" class="add_btn">添加</el-button>
          </div>
        </el-col>
        <el-col class="address_list_wrapper">
          <p class="address_title">地址列表</p>
          <el-table :data="addressList || []" v-loading="addressLoad">
            <el-table-column :show-overflow-tooltip="true" prop="coin" label="币种" width="150"> </el-table-column>
            <el-table-column :show-overflow-tooltip="true" prop="withdral_address" label="提币地址" > </el-table-column>
            <el-table-column :show-overflow-tooltip="true" prop="remark_name" label="备注" > </el-table-column>
            <el-table-column :show-overflow-tooltip="true"  label="操作" width="150">
              <template slot-scope="scope">
                <span class="opera" @click="deleteMenAddress(scope.row)">删除</span>
              </template>
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
  import Header from '@/components/header/header'
  import BreadCrumb from '@/components/breadcrumb/breadcrumb'

  export default {
    inject:['reload'],
    components: {Header,BreadCrumb},
    props: {},
    created() {
      this.analyAddress()
    },
    data() {
      return {
        crumbs:[],
        coinVal:'',
        coins:[],
        linkVal:'',
        links:[],
        addressVal:'',
        remark:'',
        addressList:[],
        addressLoad:true,
        addAddressLoad:false
      }
    },
    watch: {},
    computed: {},
    methods: {
      /* 解析路由地址 */
      analyAddress(){
        const route = this.$route
        const bread = JSON.parse(localStorage.getItem('breads'))
        var cbs = [{name:'提币地址管理',url:this.$route.fullPath}]
        cbs.unshift(bread)
        this.crumbs = cbs
      },

      /* 获取提币地址 */
      getMenAddress(){
        // var info = {"coin": coin}
        this.$store.dispatch('men_his_list').then(res => {
          if(res.data.result == 0){
            this.addressLoad = false
            this.addressList = res.data.data || []
          }
        }).catch(err => {
          this.addressLoad = false
        })
      },

      /* 添加提币地址 */
      addAddress(){
        if(!this.addAddressLoad){
          if(this.coinVal == ''){
            this.$message.info('未选择币种')
            return false
          }
          if(this.linkVal == ''){
            this.$message.info('未选择链名称')
            return false
          }
          if(this.addressVal == ''){
            this.$message.info('请输入提币地址')
            return false
          }
          if(this.remark == ''){
            this.$message.info('请输入备注')
            return false
          }
          this.addAddressLoad = true
          var info = {
            coin:this.coinVal,
            type:this.linkVal,
            withdral_address:this.addressVal, 
            remark_name:this.remark
          }

          this.$store.dispatch('add_menaddress',info).then(res => {
            this.addAddressLoad = false
            if(res.data.result == 0){
              // console.log('res添加', res)
              this.$message.success('添加成功')
              this.reload()
              // this.getMenAddress()
            }
          }).catch(err => {
            this.addAddressLoad = false

          })
        }
      },

      /* 获取币种 */
      getCoins(){
        this.$store.dispatch('coinGet').then(res => {
          if(res.data.result == 0){
            // console.log('币种res', res)
            this.coins = res.data.data || []
          }
        })
      },

      /* 选择币种和链名称 */
      sel_lcos(str){
        switch(str){
          case 'coin':
            // console.log('eeeeee',str)
            // console.log('eeeeee',this.coinVal)
            this.getLinks()
            break;
          case 'links':
            break;
          default:
            break;
        }
      },

      /* 获取链名称 */
      getLinks(){
        var info = {"coin":this.coinVal}
        this.$store.dispatch('getLinkName', info).then(res => {
          if(res.data.result == 0){
            // console.log('链名称res', res)
            this.links = res.data.data || []
          }
        })
      },

      /* 删除提币地址 */
      deleteMenAddress(obj){
        var info = {"id":obj.id,"coin":obj.coin}
        this.$confirm('确定删除该记录吗','提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$store.dispatch('delete_menaddress', info).then(res => {
            if(res.data.result == 0){
              this.getMenAddress()
            }
          }).catch(err => {

          })
        }).catch(() => {
          this.$message.warning('取消操作')
        })
      }
    },
    mounted(){
      this.getMenAddress()

    },
    created(){
      this.analyAddress()
      this.getCoins()
    }
  };
</script>
<style lang="scss" scoped>
  @import '@/style/base.scss';
  .menAddress_wrapper{
    width: 100%;
    min-height: 100%;
    background-color: $other_bg;
    min-width: 1280px;
    padding-top: 45px;
    padding-bottom: 15px;
    box-sizing: border-box;
    .menaddress_content{
      width: 1200px;
      margin: 0 auto;
      .cru_content{
        padding: 15px 0;
        box-sizing: border-box;
      }
      .menaddress_container{
        width: 100%;
        .add_address_wrapper{
          width: 100%;
          background-color: $light_font_color;
          padding: 10px 20px 30px;
          box-sizing: border-box;
          margin-bottom: 10px;
          .add_address_content{
            display: flex;
            justify-content: flex-start;
            margin-bottom: 15px;
            .com_title{
              color: $dark_font_color;
              font-size: $font14;
              line-height: 30px;
            }
            .coin_kinds,.mention_address,.link_kinds{
              margin-right: 30px;
            }
            .mention_address{
              width: 400px;
            }
            .remark{
              flex-grow: 1;
            }
          }
          .add_btn_content{
            width: 100%;
            text-align: right;
            .add_btn{
              width: 140px;
              background-color: $user_btn_bg_btn;
              color: #fff;
            }
          }
        }
        .address_list_wrapper{
          width: 100%;
          background-color: $light_font_color;
          padding: 10px 20px 30px;
          min-height: 300px;
          .address_title{
            line-height: 45px;
            color: $noraml_font_color;
            font-size: $font18;
            
          }
          .opera{
            color: $active_font_color;
            font-size: $base_font;
            margin-right: 10px;
            cursor: pointer;
          }
        }
      }
    }
  }
</style>


